@font-face {
  font-family: "Bernina Sans";
  src: url("../fonts/BerninaSans-Web-CondensedBold.woff2") format("woff2");
}

@font-face {
  font-family: "GothamHTF";
  src: url("../fonts/GothamHTF-Medium.woff2") format("woff2");
  font-weight: 400;
}

@font-face {
  font-family: 'GothamHTF';
  src: url("../fonts/GothamHTF-Light.woff2") format("woff2");
  font-weight: 200;
}

header {
  height: 700px;
  background: no-repeat center/cover url("../img/background.jpg");
}

header .promo {
  transform: rotate(20deg);
  position: absolute;
  top: 70px;
  right: 30px;
  padding: 10px;
  width: 300px;
  border-radius: 10px;
  color: #fff;
  background-color: #811226;
  font-family: "GothamHTF";
  text-align: center;
}

header .promo small {
  font-weight: 200;
  font-size: 0.6em;
}

header .accroche {
  position: absolute;
  top: 620px;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  width: 100%;
  color: #fff;
  background-color: #fff3;
  font-family: "Bernina Sans";
}

header .accroche span:last-child {
  font-size: 0.6em;
}

.container {
  padding: 10px;
  font-family: "Bernina Sans";
  color: #12465b;
}

.logo_bouton {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo_bouton .logo {
  width: 50%;
}

.logo_bouton .bouton {
  padding: 5px;
  border: 1px solid #777;
  border-radius: 10px;
  background-color: #fff;
  font-family: "GothamHTF";
  font-weight: 200;
  letter-spacing: 0.05em;
  color: inherit;
}

.presentation {
  text-align: center;
}

.presentation .txtVignette {
  font-family: "GothamHTF";
  font-weight: 200;
  letter-spacing: 0.2em;
  color: inherit;
  font-size: 0.75em;
  text-align: left;
}
